<template>
  <div class="order-detail">
    <div class="order-info ">
      <p class=" number-state">
        <span class="number">订单编号：{{orderInfo.sn}}</span>
        <span class="state">{{orderStatus(orderInfo.status)}}</span>
      </p>
      <div class="title-logo ">
        <p class="company-title">{{orderInfo.name}}</p>
        <p class="logo"><img :src="baseSrc+orderInfo.brand_logo" alt="公司logo"></p>
      </div>

      <div class="info-wrapper ">
        <p class="info" v-if="orderInfo.policy_num">保单号：{{orderInfo.policy_num}}</p>
        <p class="info">被保险人：{{orderInfo.relation_name}}</p>
        <p class="info">保障期间：{{orderInfo.policy_effective_date}}至{{orderInfo.policy_expiration_date}}</p>
        <p class="info">保险公司：{{orderInfo.brand_name}}</p>
        <!--<p class="info" v-if="orderInfo.policy_num">保单号：{{orderDetail.policy_num}}</p>-->
      </div>
      <p class="price">{{orderInfo.price}}<span>元</span></p>
    </div>
    <div class="layer bzfa-wrapper ">
      <div class="layer bzfa-layer">
        <div class="title-name " @click="bzfaShow=!bzfaShow">
          <span class="title">保障方案</span>
          <p class="name" v-if="orderInfo.plan_name">
            <span>{{orderInfo.plan_name}}</span>
            <i :style="bzfaShow ? upImg : downImg"></i>
          </p>
        </div>
        <div class="bzfa-wrapper" v-show="bzfaShow" v-for="(item,index) in planInfo" :key="index">
          <div class="plan-info-item">
            <div class="oneDiv" @click="detailShow(index)">
              <span class="bzxm">{{item.name}} </span>
              <div class="bzsm">
                <span>{{item.value}}</span>
                <i :style="moreArr[index] ? upImg : downImg"></i>
              </div>
            </div>
            <div class="twoDiv" v-show="moreArr[index]">
              <div class="bzed">
                <span>{{item.detail}}</span>
              </div>
            </div>

          </div>
        </div>

      </div>
    </div>
    <div class="other-info ">
      <div class="layer ">
        <div class="title-name" @click="ttrShow=!ttrShow">
          <span class="title">投保人信息</span>
          <p class="name">
            <span class="name">{{orderInfo.insurance_name}}</span>
            <i :style="ttrShow ? upImg : downImg"></i>
          </p>
        </div>
        <div class="info-wrapper" v-show="ttrShow">
          <p class="info info-ts"><span>姓名</span><span>{{orderInfo.insurance_name}}</span></p>
          <p class="info info-ts"><span>性别</span><span>{{getSexByName(orderInfo.insurance_gender)}}</span></p>
          <p class="info info-ts"><span>身份证号</span><span>{{orderInfo.insurance_id_num}}</span></p>
          <p class="info info-ts"><span>出生日期</span><span>{{dateFormat(orderInfo.insurance_birth)}}</span></p>
          <p class="info info-ts"><span>联系电话</span><span>{{orderInfo.insurance_mobile}}</span></p>
        </div>
      </div>
      <div class="layer layer-ts ">
        <div class="title-name" @click="bttrShow=!bttrShow">
          <span class="title">被保险人信息</span>
          <p class="name">
            <span class="name">{{orderInfo.relation_name}}</span>
            <i :style="bttrShow ? upImg : downImg"></i>
          </p>
        </div>
        <div class="info-wrapper" v-show="bttrShow">
          <p class="info info-ts"><span>姓名</span>{{orderInfo.relation_name}}</p>
          <p class="info info-ts"><span>性别</span>{{getSexByName(orderInfo.relation_gender)}}</p>
          <p class="info info-ts"><span>身份证号</span>{{orderInfo.relation_id_num}}</p>
          <p class="info info-ts"><span>出生日期</span>{{dateFormat(orderInfo.relation_birth)}}</p>
          <p class="info info-ts"><span>是投保人的</span>{{getRelation(orderInfo.relation_type)}}</p>
        </div>
      </div>
      <div class="layer ">
        <p class="title-name ">
          <span class="title">受益人</span>
          <span class="name name-syr">{{orderInfo.beneficiary}}</span>
        </p>
      </div>
      <div class="layer layer-order">
        <p class="title ">
          订单信息
        </p>
        <div class="info-wrapper">
          <div class="wrapper"
               v-if="orderInfo.status===1||orderInfo.status===2||orderInfo.status===3||orderInfo.status===4||orderInfo.status===5||orderInfo.status===6">
            <p class="info" v-if="payInfo"><span>支付方式</span>{{payInfo.paymentPluginName}}</p>
            <p class="info"  v-if="payInfo"><span>支付金额</span>¥{{payInfo.amount}}</p>
            <p class="info"  v-if="payInfo"><span>支付流水号</span>{{payInfo.transactionId}}</p>
            <p class="info"  v-if="payInfo"><span>付款时间</span>{{payInfo.create_time}}</p>
          </div>
          <p class="info"><span>订单编号</span>{{orderInfo.sn}}</p>
          <p class="info"><span>创建时间</span>{{orderInfo.create_time}}</p>
        </div>
      </div>
      <div class="btns">
        <div class="btn baodan" v-if="orderInfo.policy_url!==null">电子保单</div>
        <div class="btn againPay" v-if="[5,6,7].filter(i=>i===orderInfo.status).length" @click='handleRePay(orderInfo)'>
          再次购买
        </div>
        <div class="btn toSettle"  @click='handleClaim(orderInfo)'
             v-if="[4].filter(i=>i===orderInfo.status).length || ([5].filter(i=>i===orderInfo.status).length && dateDiff(orderInfo.policy_expiration_date))">
          申请理赔
        </div>
        <div class="btn toPay" v-if="orderInfo.status===0" @click='handleToPay(orderInfo)'>立即支付</div>
        <div class="btn rePay" v-if="[8].filter(i=>i===orderInfo.status).length" @click='handleToPay(orderInfo)'>重新支付
        </div>
        <div class="btn toDetail" v-if="[0,1,2,3,4,5,6,7,8].filter(i=>i===orderInfo.status).length"
             @click="goDetail(orderInfo)">查看详情
        </div>
      </div>
    </div>
  </div>
</template>

<script>

import { orderStatus, getSexByName, dateFormat, getRelation } from '@/assets/js/mixins/base.js'

export default {
  mixins: [orderStatus, getSexByName, dateFormat, getRelation],
  data () {
    return {
      // moreArr: [],
      bzfaShow: true,
      bttrShow: false,
      ttrShow: false,
      orderInfo: {},
      bzfaInfo: {},
      payInfo: {},
      planInfo: [],
      baseSrc: '',
      moreArr: [],
      upImg: {
        'background-image':
          'url(' + require('@/assets/images/icon_up.png') + ')'
      },
      downImg: {
        'background-image':
          'url(' + require('@/assets/images/icon_down.png') + ')'
      }
    }
  },
  mounted () {
    this.getOrderDetail()
    this.baseSrc = `${process.env.VUE_APP_RESOURCE_URL}`
    window.diff=this.dateDiff
    //
  },
  methods: {
    handleRePay (item) { // 再次购买
      this.$router.push({
        query: {
          productId: item.product_id,
          orderSn: item.sn
        },
        path: `/product/detail/${item.product_id}`
      })
    },
    handleClaim (item) { // 申请理赔
      this.$router.push({
        path: `/claimList/claimApply`,
        query: {
          orderSn: item.sn,
          type: 0
        }
      })
    },
    handleToPay (item) { // 重新支付 //立即支付
      this.$router.push({
        query: {
          sn: item.sn,
          productId: item.product_id
        },
        path: `/product/orderConfirm`
      })
    },
    goDetail (item) {
      this.$router.push({
        path: `/product/detail/${item.id}`,
        query: {
          orderId: item.id,
          productId: item.product_id
        }
      })
    },
    dateDiff(odate){
      if(!odate)return false;
      var dateStr = new Date().getFullYear() + '-' + (new Date().getMonth()+1) + '-' + new Date().getDate();
      var odate = odate.split(' ')[0]
      let  Date1  =  new  Date(dateStr),
        Date2  =  new  Date(odate);
      let  iDays  =  parseInt(Math.abs(Date1  -  Date2)  /  1000  /  60  /  60  /24)    //把相差的毫秒数转换为天数  
      console.log(iDays)
      return  iDays <= 200
    },
    detailShow (index) {
      this.moreArr.forEach((item, i) => { // 循环已经把所有的状态值清空了
        if (index !== i) {
          this.moreArr.splice(i, 1, false)
        }
      })
      this.moreArr.splice(index, 1, !this.moreArr[index])
    },
    getOrderDetail () {
      let postData = {
        orderId: this.$route.query.orderId
      }
      this.$api.personCenter.orderDetail(postData).then(res => {
        if (res.code === 1) {
          this.orderInfo = res.data
          this.planInfo = res.data.plan_details
          this.payInfo = res.data.payment_info
          // this.moreArr.push(false)
          for (let i = 0; i < this.planInfo.length; i++) {
            this.moreArr.push(false)
            this.$set(this.planInfo[i], 'detail', res.data.parameter_values[0].entries[i].value)
          }
        }
      })
    }
  }
}
</script>

<style scoped lang="scss">
  @import "@/assets/styles/mixin.scss";

  .order-detail {
    position: fixed;
    z-index: 100;
    top: 0.84rem;
    left: 0;
    bottom: pxToRem(100px);
    right: 0;
    background: #f5f5f5;
    overflow-y: scroll;
    .order-info {
      background: #fff;
      padding-left: pxToRem(30px);
      margin-bottom: pxToRem(20px);
      .number-state {
        font-size: pxToRem(30px);
        color: #A5A8A8;
        height: pxToRem(82px);
        line-height: pxToRem(82px);
        border-bottom: $border-bottom-light-color;
        box-sizing: border-box;
        display: flex;
        justify-content: space-between;
        align-items: center;
        .number {
          flex: 1;
        }
        .state {
          width: pxToRem(140px);
          padding-right: pxToRem(30px);
          text-align: center;
        }
      }
      .title-logo {
        margin-top: pxToRem(30px);
        position: relative;
        display: flex;
        justify-content: space-between;
        align-items: center;
        padding-right: pxToRem(30px);
        .company-title {
          font-size: pxToRem(36px);
          color: $color-text;
          flex: 1;
          height: pxToRem(60px);
        }
        .logo {
          width: pxToRem(110px);
          height: pxToRem(60px);
          img {
            display: block;
            width: pxToRem(110px);
            height: pxToRem(60px);
          }
        }
      }
      .price {
        font-size: pxToRem(40px);
        color: $color-active-text;
        height: pxToRem(90px);
        line-height: pxToRem(90px);
        span {
          font-size: pxToRem(24px);
          color: #C5C5C5;
        }
      }
      .info-wrapper {
        position: relative;
        .info {
          font-size: pxToRem(26px);
          line-height: pxToRem(42px);
          width: 100%;
        }
        .info-ts {
          font-size: pxToRem(24px);
          line-height: pxToRem(38px);
        }

      }
    }
    .bzfa-layer {
      .bzfa-wrapper {
        padding-left: pxToRem(30px);
        background: #F1F4F4;
        .plan-info-item {
          .oneDiv {
            display: flex;
            justify-content: space-between;
            border-bottom: 1px solid #D5D8D8;
            padding-right: pxToRem(20px);
            height: pxToRem(86px);
            line-height: pxToRem(86px);
          }
          .twoDiv {
            font-size: pxToRem(24px);
            color: #A5A8A8;
            line-height: pxToRem(38px);
            border-bottom: 1px solid #D5D8D8;
            padding: pxToRem(12px) pxToRem(30px) pxToRem(12px) 0;
          }
          .bzsm {
            display: flex;
            justify-content: space-between;
            align-items: center;
            i {
              display: inline-block;
              width: pxToRem(44px);
              height: pxToRem(44px);
              background-repeat: no-repeat;
              background-position: right center;
              background-size: cover;
            }
          }

        }
        &:last-child {
          .plan-info-item {
            .twoDiv {
              border-bottom: none;
            }
          }

        }
      }

    }
    .layer {
      background: #fff;

      &:last-child {
        margin-right: 0;
      }
      .title-name {
        padding-left: pxToRem(30px);
        display: flex;
        justify-content: space-between;
        border-bottom: $border-bottom-light-color;
        padding-right: pxToRem(20px);
        height: pxToRem(91px);
        line-height: pxToRem(91px);
        .title {
          font-size: pxToRem(28px);
          color: $color-text;
          box-sizing: border-box;

        }
        .name {
          font-size: pxToRem(28px);
          color: $color-text;
          display: flex;
          justify-content: flex-end;
          align-items: center;
          i {
            display: inline-block;
            width: pxToRem(44px);
            height: pxToRem(44px);
            background-repeat: no-repeat;
            background-position: right center;
            background-size: cover;
          }
        }
        .name-syr {
          padding-right: pxToRem(30px);
        }
      }
      .info-wrapper {
        position: relative;
        border-bottom: $border-bottom-light-color;
        padding: pxToRem(16px) pxToRem(42px) pxToRem(17px) pxToRem(36px);
        background: #F1F4F4;
        .info {
          font-size: pxToRem(26px);
          color: #A5A8A8;
          line-height: pxToRem(42px);
          display: flex;
          justify-content: space-between;
          width: 100%;
          .tbr {
            margin-right: 110px;
          }
        }
        .info-ts {
          font-size: pxToRem(24px);
          line-height: pxToRem(38px);
        }
        .info-lsh {
          line-height: 24px;
          margin-top: -6px;
          margin-bottom: 6px;
        }

      }

    }
    .layer-order {
      background: #FFFFFF;
      margin-top: pxToRem(20px);
      .title {
        font-size: pxToRem(28px);
        color: $color-text;
        position: relative;
        padding: pxToRem(30px) 0 pxToRem(14px) pxToRem(74px);
        &:before {
          content: '';
          position: absolute;
          top: pxToRem(42px);
          left: pxToRem(30px);
          width: pxToRem(28px);
          height: pxToRem(4px);
          background: $color-active-text;
        }
      }
      .info-wrapper {
        padding-left: pxToRem(30px);
        background: #fff;
      }
    }
    .btns {
      position: fixed;
      bottom: 0;
      width: 100%;
      /*margin-top: pxToRem(20px);*/
      background: #fff;
      height: pxToRem(100px);
      line-height: pxToRem(100px);
      display: flex;
      justify-content: flex-end;
      align-items: center;
      box-shadow: 0 -4px 16px 0 rgba(225, 225, 225, 0.64);
      .btn {
        width: pxToRem(150px);
        height: pxToRem(60px);
        line-height: pxToRem(60px);
        margin-right: pxToRem(20px);
        font-size: pxToRem(24px);
        border-radius: pxToRem(34px);
        text-align: center;
        color: #B6BBBB;
        &.toDetail {
          color: #FFFFFF;
          background: #1FD8D2;
          border: 1px solid $color-active-text;
        }
        &.toPay, &.againPay, &.rePay {
          color: #FF4B4B;
          background: #FFFFFF;
          border: 1px solid #FF4B4B;
        }
        &.baodan {
          color: #A5A8A8;
          border: 1px solid #D5D8D8;
          background: #FFFFFF;
        }
        &.toSettle {
          color: #1FD8D2;
          border: 1px solid #1FD8D2;
          background: #FFFFFF;
        }
      }
    }
  }
</style>
